.header__container {

  padding-top: 20px;
  @media screen and (min-width: 768px) {
    padding-top: 32px;
  }
}

.header__background {
  padding: 14px 14px 20px 14px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  max-width: 280px;
  border-radius: 10px;
  min-height: 460px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  background-image: linear-gradient(
      to right,
      var(--overlay-color),
      var(--overlay-color)
    ),
    url(../images/background-mobile.jpg);

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        to right,
        var(--overlay-color),
      var(--overlay-color)
      ),
      url(../images/background-mobile@2x.jpg);
  }
  @media (min-device-pixel-ratio: 3),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        to right,
        var(--overlay-color),
      var(--overlay-color)
      ),
      url(../images/background-mobile@3x.jpg);
  }

  @media screen and (min-width: 768px) {
    min-width: 704px;
    min-height: 222px;
    padding: 16px;
    border-radius: 15px;

    background-image: linear-gradient(
        to right,
        var(--overlay-color),
      var(--overlay-color)
      ),
      url(../images/background-tab.jpg);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          var(--overlay-color),
      var(--overlay-color)
        ),
        url(../images/background-tab@2x.jpg);
    }
    @media (min-device-pixel-ratio: 3),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
         var(--overlay-color),
      var(--overlay-color)
        ),
        url(../images/background-tab@3x.jpg);
    }
  }

  @media screen and (min-width: 1280px) {
    min-width: 1216px;
    min-height: 316px;
    padding: 16px 32px 32px 32px;
    border-radius: 15px;
    background-image: linear-gradient(
        to right,
        var(--overlay-color),
      var(--overlay-color)
      ),
      url(../images/background-desk.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          var(--overlay-color),
      var(--overlay-color)
        ),
        url(../images/background-desk@2x.jpg);
    }
    @media (min-device-pixel-ratio: 3),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          var(--overlay-color),
      var(--overlay-color)
        ),
        url(../images/background-desk@3x.jpg);
    }
  }
}

.header__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 8px;
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: flex-end;
    gap: 8px;
    @media screen and (min-width: 1280px) {
      padding-top: 0;
      gap: 8px;
    }
  }
}



.button {
  padding: 0;
  border-color: inherit;
  border: transparent;
  background: transparent;
}

.header__btn-wrapper {
  display: flex;
  gap: 4px;
  @media screen and (min-width: 768px) {
    margin-bottom: 0px;
    margin-top: 0;
    padding-top: 0;
     
  }
}

.header__btn-home.current,
.header__btn-library.current {
  color: var(--primary-text-color);
  background: var(--secondary-text-color);
}
.header__btn-home {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 26px;
  width: 87px;
  height: 34px;
  border-radius: 100px;

  font-weight: 500;
  font-size: 12px;
  line-height: calc(12 / 14);
  border: transparent;
  text-transform: uppercase;
  cursor: pointer;
 color: var(--secondary-text-color);
  background: var(--primary-text-color);
  @media screen and (min-width: 768px) {
    padding: 10px 20px;
    width: 83px;
    height: 38px;
   
  }
  @media screen and (min-width: 1280px) {
    padding: 14px 30px;
    width: 99px;
    height: 42px;
  
  }
}

.header__btn-library {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 113px;
  height: 34px;
  margin-left: 4px;
  border-radius: 100px;

  font-weight: 500;
  font-size: 12px;
  line-height: calc(12 / 14);
  text-transform: uppercase;
  cursor: pointer;
  color: var(--secondary-text-color);
  background: var(--primary-text-color);
  @media screen and (min-width: 768px) {
    padding: 10px 20px;
    width: 109px;
    height: 38px;
    
  }
  @media screen and (min-width: 1280px) {
    padding: 14px 24px;
    // margin-left: 8px;
    width: 117px;
    height: 42px;
  
  }
}

.header__form {
  display: flex;
  justify-content: space-between;
  width: 252px;
  background: var(--primary-text-color);
  border-radius: 100px;
  @media screen and (min-width: 768px) {
    width: 165px;
    height: 38px;
  }
  @media screen and (min-width: 1280px) {
    width: 187px;
    height: 42px;
  }
}

.header__form-input {
  height: 34px;
  padding-left: 14px;
  border-radius: 100px;
  border: transparent;
  outline: transparent;
  

  @media screen and (min-width: 768px) {
    max-width: 120px;
    height: 38px;
  }

  color: var(--secondary-text-color);
  @media screen and (min-width: 1280px) {
    max-width: 142px;
    height: 42px;
  }
}


.header__form-btn {
  width: 45px;
  padding: 7px;
  cursor: pointer;
  background-color: var(--primary-text-color);
  border: transparent;

  justify-content: center;
  align-items: center;
  display: flex;
  border-top-right-radius: 100px;
  border-bottom-right-radius: 100px;
}

.header__form-input::placeholder {
  font-weight: 400;
  font-size: 12px;
  line-height: calc(12 / 14);

  color: var(--secondary-text-color);
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(14 / 16);
  }
}
.header__form-btn {
  border-left: 0;
  border-color: inherit;
}
.header__form-svg {
  display: inline-block;
  width: 14px;
  height: 14px;
  stroke-width: 0;
  stroke: currentColor;
  stroke: var(--secondary-text-color);
  fill: var(--primary-text-color);
  @media screen and (min-width: 768px) {
    width: 16px;
    height: 16px;
  }
}

.header__btn-watched {
  padding: 10px 23px;
  width: 102px;
  height: 34px;
  border-radius: 100px;

  font-weight: 500;
  font-size: 12px;
  line-height: calc(12 / 14);
  text-transform: uppercase;
  cursor: pointer;
  color: var(--current-color);
  background: var(--primary-text-color);

  @media screen and (min-width: 768px) {
    height: 38px;
    padding: 12px 23px;
  }
  @media screen and (min-width: 1280px) {
    margin-right: 8px;
    width: 120px;
    height: 42px;
    padding: 14px 32px;
  }
}
.header__btn-queue {
  padding: 10px 24px;
  width: 86px;
  height: 34px;
  border-radius: 100px;

  font-weight: 500;
  font-size: 12px;
  line-height: calc(12 / 14);
  text-transform: uppercase;
  cursor: pointer;
  color: var(--current-color);
  background: var(--primary-text-color);

  @media screen and (min-width: 768px) {
    padding: 12px 24px;
    height: 38px;
  }
  @media screen and (min-width: 1280px) {
    height: 42px;
    padding: 14px 24px;
  }
}

.header__btn-category--current {
  color: var(--primary-text-color);
  background: var(--current-color);
}

.header__logo-link {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.header__film-svg {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin-right: 8px;
  stroke-width: 0;
  stroke: currentColor;
  stroke: var(--primary-text-color);
  fill: transparent;
  @media screen and (min-width: 768px) {
    width: 40px;
    height: 40px;
  }
  @media screen and (min-width: 1280px) {
    width: 59px;
    height: 59px;
    margin-right: 16px;
  }
}

.header__title {
  font-weight: 500;
  font-size: 32px;
  line-height: 38px;
  line-height: calc(32 / 38);
  color: var(--primary-text-color);
  @media screen and (min-width: 768px) {
    font-size: 40px;
    line-height: calc(40 / 47);
  }
  @media screen and (min-width: 1280px) {
    font-size: 64px;
    line-height: calc(64 / 75);
  }
}

.header__notifier {
  display: block;
  position: absolute;
top: 85px;
left: 10px;
margin: 0 auto;
font-style: normal;
font-weight: 400;
font-size: 10px;
line-height: 12px;
width: 155px;


color: var(--primary-text-color);

@media screen and (min-width: 768px) {
top: 50px;

}
@media screen and (min-width: 1280px) {
 top: 55px;
}
}

.header__notifier.is-hidden {
 display: none;
}


.header__btn {
  margin-top: 16px;
  transition: 1s ease-in-out;
  @media screen and (min-width: 768px) {
    display: flex;
    margin-top: 0;
    display: none;
   
  }
}
.header__btn-top {
  transition: 2s ease-in-out;
  @media screen and (max-width: 768px) {
    display: none;
  }
  margin-left: auto;
}
.header__btn-bottom {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    padding-bottom: 0;
  }
}
