.header__film-svg {
  -webkit-animation: rotate-vert-center .5s cubic-bezier(.455, .03, .515, .955) both;
  animation: rotate-vert-center 1.5s cubic-bezier(.455, .03, .515, .955) both;
}

@keyframes rotate-vert-center {
  0% {
    -webkit-transform: rotateY(0);
    transform: rotateY(0);
  }

  100% {
    transform: rotateY(360deg);
  }
}

.focus-in-expand-fwd {
  animation: focus-in-expand-fwd .8s cubic-bezier(.25, .46, .45, .94) both;
}

@keyframes focus-in-expand-fwd {
  0% {
    letter-spacing: -.5em;
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
    transform: translateZ(-800px);
  }

  100% {
    -webkit-filter: blur();
    filter: blur();
    opacity: 1;
    transform: translateZ(0);
  }
}

